<template>
  <div>
    <Header></Header>
    <main class="main dashboard-main">
      <div class="container-fluid" style="padding-top: 30px" v-loading="load">
        <div class="animated fadeIn">
          <!--content-header-->
          <div class="row">
            <div class="col">
              <div class="">
                <!--card-header-->
                <div class="card-body">
                  <div class="row">
                    <!-- <div class="col-md-3" v-for="item in Cursos" :key="item.ID">
                      {{item}}
                    </div> -->
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="container-fluid">
            <div class="row">
              <div class="col-md-12">
                <div class="course-details-content">
                  <h1>
                    {{ AulaAtual.NAME }}
                  </h1>
                  <h2>{{ Actual.NAME }}</h2>
                </div>
              </div>
              <div class="col-md-8">
                <div class="course-details-item border-bottom-0 mb-0">
                  <div class="course-single-pic mb30"></div>
                  <div class="course-single-text">
                    <div class="course-details-content">
                      <br />
                      <br />
                      <button @click="play">play</button>
                      <button @click="getTime">getTime</button>
                      <p style="margin-top: 30px" v-if="AulaAtual.URL">
                        <vimeo-player
                          ref="player"
                          :video-id="link"
                          :player-height="height"
                          @ready="onReady"
                          @timeupdate="getTime()"
                          @ended="closeFullWindow()"
                        />
                      </p>
                    </div>
                  </div>
                </div>

                <div class="affiliate-market-guide mb65"></div>
              </div>

              <div class="col-md-4">
                <div class="side-bar">
                  <div class="course-details-category ul-li">
                    <p id="nextButton"></p>
                    <span class="float-none"><b>Aulas deste curso: </b></span>
                    <div class="lista">
                      <div
                        id="accordion"
                        class="panel-group"
                        v-for="item in Cursos"
                        :key="item.ID"
                        @click.prevent="GO(item)"
                      >
                        <div
                          :class="
                            item.STATUS_ID == 2
                              ? 'concluido panel position-relative '
                              : item.STATUS_ID == 1
                              ? 'andamento panel position-relative'
                              : 'panel position-relative'
                          "
                        >
                          <div
                            class="position-absolute"
                            style="right: 0; top: 0px"
                          >
                            <span
                              v-if="item.STATUS_ID == 2"
                              id="span"
                              class="p-1 text-white font-weight-bold completed"
                            >
                              {{ item.STATUS_ID == 2 ? "Concluido" : "" }}</span
                            >
                          </div>
                          <div class="panel-title">
                            <div class="ac-head">
                              <button class="btn btn-link">
                                <span class="seq">{{ item.SEQ }}</span>
                                {{ item.NAME }}
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div class="course-side-bar-widget">
                      <a
                        @click="nextSeq()"
                        href="#"
                        class="
                          genius-btn
                          btn-block
                          text-white
                          gradient-bg
                          text-center text-uppercase
                          bold-font
                        "
                      >
                        {{ "Próxima Aula" }}
                        <i class="fa fa-arow-right"></i
                      ></a>

                      <a
                        class="btn btn-danger btn-block text-white genius-btn"
                        href="#"
                        @click.prevent="goTO('/dashboard')"
                        ><i class="fa fa-arrow-left"></i>
                        Voltar
                      </a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
  </div>
</template>
<script>
// @ is an alias to /src
import { reactive, ref } from "vue";
import Header from "../../components/Header/index.vue";
import Footer from "../../components/Footer/index.vue";
import Routes from "../../services/Routes";
import * as moment from "moment";
import Swal from "sweetalert2";
import { vueVimeoPlayer } from "vue-vimeo-player";
import Player from "@vimeo/player";
import AOS from "aos";

export default {
  name: "Home",
  components: {
    Header,
    Footer,
    vueVimeoPlayer,
  },

  data() {
    return {
      menu: true,
      Cursos: false,
      ID: false,
      AulaAtual: false,
      key: false,
      TIME: false,
      load: false,
      Actual: false,
      height: 500,
      link: 646438431,
      options: {
        muted: true,
        autoplay: true,
        progress: true,
      },
      playerReady: false,
    };
  },
  watch: {
    "$route.params.id": function () {
      this.load = true;
      window.scrollTo(0, 0);
      if (this.$route.params.id.length > 0) {
        this.ID = this.$route.params.id;
      }
      if (this.$route.params.key.length > 0) {
        this.key = this.$route.params.key;
      }
      console.log(this.ID, this.key);
      this.getCourses();
      this.ConfirmLesson();
    },
  },
  methods: {
    onReady() {
      this.playerReady = true;
    },
    play() {
      this.$refs.player.play();
      console.log("iniciou vídeo");
      console.log(this.$refs.player);
    },
    getTime(dado) {
this.$refs.vimeo.player.setCurrentTime(1)
    },
    closeFullWindow(dado) {
      console.log("atualizou", dado);
    },
    GO(item) {
      if (item.STATUS_ID == 2) {
        this.goTO(
          "/lesson/" + item.COURSE_CLASS_USER_ID + "/" + item.COURSE_ID
        );
      }
    },
    nextLesson(dados) {
      let final = "";
      let i = 0;
      for (; i <= dados.length - 1; i++) {
        if (dados[i].COURSE_CLASS_USER_ID == this.ID) {
          final = dados[i];
        }
      }
      this.AulaAtual = final;
      this.load = false;
    },
    nextSeq() {
      this.load = true;
      console.log("atual aula", this.AulaAtual);
      let data = {
        P_COURSE_CLASS_USER_ID: this.ID,
      };
      Routes.FinishLesson(data)
        .then((r) => {
          let dados = this.Cursos;
          let Seq = this.AulaAtual.SEQ + 1;
          let nova = false;
          let i = 0;
          for (; i <= dados.length - 1; i++) {
            if (dados[i].SEQ == Seq) {
              console.log("próxima aula", dados[i]);
              nova = dados[i];
            } else {
              console.log("seq não encontrado");
            }
          }
          if (nova) {
            this.goTO(
              "/lesson/" + nova.COURSE_CLASS_USER_ID + "/" + nova.COURSE_ID
            );
          }
        })
        .catch((e) => {
          console.log(e);
        });
    },
    getDescription: function (name) {
      if (name.length > 250) {
        return name.substring(0, 250) + "...";
      }
      return name;
    },
    getURL(item) {
      var a = "";
      for (var obj in item) {
        a = item[obj];
        if (a.includes("https", 0) == true) {
          return a;
        } else {
          return null;
        }
      }
    },
    goTO(url) {
      this.$router.push(url);
    },
    getCourses() {
      let data = {
        P_USER_ACCOUNT_ID: JSON.parse(
          localStorage.getItem("SELECTED_ACCOUNT_ID")
        )
          ? JSON.parse(localStorage.getItem("SELECTED_ACCOUNT_ID"))
          : 0,
        P_USER_ID: JSON.parse(localStorage.getItem("userID")),
        P_COURSE_ID: this.key,
      };
      Routes.getLessons(data)
        .then((r) => {
          this.Cursos = r.data.DATA;
          this.nextLesson(r.data.DATA);
        })
        .catch((e) => {
          console.log(e);
        });
    },
    ConfirmLesson() {
      let data = {
        P_COURSE_CLASS_USER_ID: this.ID,
      };
      Routes.ConfirmLesson(data)
        .then((r) => {
          console.log("aula confirmada");
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.getCourses();
        });
    },
    changeActive: function () {
      setInterval(function () {
        console.log(document.getElementById("player"));
      }, 1000);
    },
    getActual() {
      let data = {
        USER_ACCOUNT_ID: JSON.parse(localStorage.getItem("SELECTED_ACCOUNT_ID"))
          ? JSON.parse(localStorage.getItem("SELECTED_ACCOUNT_ID"))
          : 0,
        USER_ID: JSON.parse(localStorage.getItem("userID")),
      };
      Routes.getCourses(data)
        .then((r) => {
          let dados = r.data.DATA;
          let final = "";
          let i = 0;
          for (; i <= dados.length - 1; i++) {
            if (dados[i].COURSE_ID == this.key) {
              final = dados[i];
            }
          }
          this.Actual = final;
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },

  mounted() {
    this.getCourses();
    AOS.init();
    this.ConfirmLesson();
    this.getActual();
    this.onReady();
  },

  created() {
    this.load = true;
    window.scrollTo(0, 0);
    if (this.$route.params.id.length > 0) {
      this.ID = this.$route.params.id;
    }
    if (this.$route.params.key.length > 0) {
      this.key = this.$route.params.key;
    }
  },
  setup() {},
};
</script>

